import React, { Component } from "react";
import User from "./User";
import AccountStyles from "./styles/AccountStyles";

export default class Account extends Component {
  render() {
    return (
      <User>
        {({ data: { me } }) => (
          <AccountStyles>
            <h3>Hi, {me.name}</h3>
            <p>
              Your Permissions:{" "}
              {me.permissions.map(permission => `${permission}  | `)}
            </p>
          </AccountStyles>
        )}
      </User>
    );
  }
}
